<template>
  <div class="main-container">
    <div
      class="market-service"
      ref="marketServiceRef"
      :style="'width:' + paramsData.ulWidth"
    >
      <ul class="product-list" v-if="pageData.list.length">
        <li
          class="product-item"
          v-for="(item, index) in pageData.list"
          :key="index"
        >
          <div class="name-function">
            <h6>{{ item.productName }}</h6>
            <p>{{ item.productDescribe }}</p>
          </div>
          <div class="introduce">
            <p class="title">产品介绍</p>
            <ul class="function-list" v-if="item.appList.length">
              <li
                class="function-item"
                v-for="(sonItem, sonIndex) in item.appList"
                :key="sonIndex"
              >
                <svg class="icon" aria-hidden="true">
                  <use xlink:href="#icon-selected"></use>
                </svg>
                <span>{{ sonItem.appName }}</span>
              </li>
            </ul>
            <p class="not-app" v-else>暂无</p>
          </div>
          <div class="button-group">
            <span @click="handleJumpPage(item.productId)">查看详情</span>
            <span @click="handleJumpPage(item.productId)">立即购买</span>
          </div>
        </li>
      </ul>
      <el-skeleton v-else :rows="10" />
      <!--   分页   -->
      <PagePagination
        :singlePage="true"
        :pageTotal="pageData.pageTotal"
        :pageSize="pageData.pageSize"
        :pageNumber="pageData.pageNumber"
        @emitPageNumberChange="pageNumberChange"
        layout="total, prev, pager, next"
      ></PagePagination>
    </div>
  </div>
</template>
<script lang="ts" setup>
import { ref, onMounted, shallowReactive } from "vue";

import { usePage } from "jz-tool-lib";
import { useVueRouter } from "@assets/useHook";

import { productListApi } from "@api/market-service-api";

defineOptions({
  name: "marketService.applicationCenter.list",
});

const { goPage } = useVueRouter();
const marketServiceRef = ref();
const paramsData = shallowReactive({
  ulWidth: "100%",
});
const { pageNumberChange, pageData } = usePage({
  listApi: productListApi,
  props: {
    pageNumber: "current",
    pageSize: {
      name: "size",
      default: 8,
    },
  },
});
const handleJumpPage = (productId) => {
  goPage(`/marketService/applicationCenter/detail?productId=${productId}`);
};
onMounted(() => {
  paramsData.ulWidth =
    Math.floor(marketServiceRef.value.offsetWidth / 344) * 344 + "px";
});
</script>
<style lang="scss" scoped>
.market-service {
  max-width: 100%;
  margin: 0 auto;
}
.product-list {
  display: flex;
  flex-wrap: wrap;
  .product-item {
    width: 320px;
    margin-top: 32px;
    margin-right: 12px;
    margin-left: 12px;
    border-radius: 8px;
    overflow: hidden;
    background-color: #ffffff;
  }
  .name-function {
    height: 132px;
    padding: 30px 36px 0;
    border-bottom: 1px solid #edf1f6;
    h6 {
      height: 52px;
      font-size: 26px;
      font-weight: 600;
      color: #242934;
      line-height: 1;
      margin-bottom: 10px;
      @include lineClamp();
    }
    p {
      font-size: 14px;
      color: #9ca6b9;
      line-height: 18px;
      display: -webkit-box;
      -webkit-box-orient: vertical;
      -webkit-line-clamp: 2;
      overflow: hidden;
    }
  }
  .introduce {
    height: 205px;
    padding: 32px 36px 0;
    .title {
      font-size: 14px;
      color: #9ca6b9;
      line-height: 1;
      margin-bottom: 25px;
    }
    .function-item {
      display: flex;
      align-items: center;
      font-size: 14px;
      color: #242934;
      line-height: 1;
      margin-bottom: 22px;
      &:last-child {
        margin-bottom: 0;
      }
      svg {
        width: 16px;
        height: 16px;
        margin-right: 12px;
      }
    }
    .not-app {
      color: #242934;
      font-size: 14px;
    }
  }
  .button-group {
    display: flex;
    span {
      flex: 1;
      display: flex;
      align-items: center;
      justify-content: center;
      height: 44px;
      font-size: 14px;
      cursor: pointer;
      &:nth-child(1) {
        color: #0056ff;
        background: #e2ecff;
      }
      &:nth-child(2) {
        color: #ffffff;
        background: #0056ff;
      }
    }
  }
}
</style>
